body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3eee6;
}

*,
::after,
::before {
  box-sizing: border-box;
}


.main-page {
  max-width: 1100px;
  margin: 0 auto;
  padding: 15px; }
  .main-page__breadcrumbs {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px; }
  .main-page__title {
    font-size: 20px;
    font-weight: 500;
    margin: 0 0 20px 0; }
  .main-page__files-title {
    color: #000;
    font-size: 16px;
    font-weight: 500;
    margin: 0 0 14px 0; }
  .main-page__content-inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between; }
  .main-page__section {
    max-width: 560px;
    width: 100%; }
  .main-page__wrapper {
    width: 100%; }
  .main-page__section-wrapper {
    min-height: 300vh;
    background-color: #fff;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    padding: 33px 40px 40px 40px;
    border-radius: 4px; }
  .main-page__aside {
    min-width: 185px;
    max-width: 185px;
    padding-top: 50px;
    position: -webkit-sticky;
    position: sticky;
    top: 0; }
  .main-page__btn {
    background-color: #0050b3;
    color: #fff;
    font-size: 12px;
    margin-right: 5px; }
  .main-page__files {
    width: 152px; }
  .main-page__dragger {
    margin-bottom: 13px; }
  .main-page__back {
    background: transparent;
    border: none;
    margin-right: 10px;
    box-shadow: none; }
  .main-page__inputs-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, 178px);
    grid-gap: 13px; }
  .main-page__radio-title {
    display: block;
    color: #262626;
    font-size: 14px;
    margin-bottom: 4px; }
  .main-page .ant-upload-text {
    color: #262626 !important;
    font-family: Roboto;
    font-size: 14px !important; }
  .main-page .ant-upload-hint {
    color: #8c8c8c !important;
    font-family: Roboto;
    font-size: 12px !important; }

.site-header {
  background-color: #0050b3; }
  .site-header__inner {
    max-width: 1100px;
    margin: 0 auto;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .site-header__menu {
    display: flex;
    align-items: center; }
    .site-header__menu .ant-dropdown-link.ant-dropdown-trigger {
      display: flex;
      align-items: center; }
  .site-header__username {
    color: #ffffff; }
  .site-header__avatar {
    margin-right: 8px; }
  .site-header__save {
    background: transparent;
    border: none;
    margin-right: 10px;
    color: #fff; }
  .site-header__delete {
    background: transparent;
    border: none; }
  .site-header__logo-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 322px;
    width: 100%; }

.site-input {
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .site-input__title {
    color: #262626;
    font-size: 14px; }
  .site-input__tip {
    color: #8c8c8c;
    font-size: 14px; }

.site-steps .ant-steps-item-process .ant-steps-item-icon {
  background-color: #0050b3 !important;
  border: none !important; }

.site-steps .ant-steps-item {
  padding-bottom: 10px; }

.site-steps .ant-steps-item-subtitle {
  margin-left: 0; }

.site-steps .ant-steps-item-title {
  display: flex;
  flex-direction: column; }

.site-steps .ant-steps-item-active .ant-steps-item-tail::after {
  background-color: #0050b3;
  width: 2px !important; }

